.first {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  margin: 0;
  padding: 0;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  background: url("../../images/background_1stPage.jpg") no-repeat center fixed;
  background-size: cover;
  cursor: default;

  .header {
    width: 100%;

    .title {
      font-size: 48px;
      font-style: italic;
      font-family: "Arial", sans-serif;
      color: #fff;
      text-align: center;
      text-transform: uppercase;
      letter-spacing: 20px;
      position: relative;
      padding: 0 27px 27px;
      overflow: hidden;
      width: 100%;
      background: linear-gradient(90deg, #0ff, #f0f, #ff0);
      background-size: 300%;
      -webkit-background-clip: text;
      background-clip: text;
      color: transparent;
      animation: gradientShift 4s ease infinite;

      &::before {
        content: "";
        position: absolute;
        left: 50%;
        bottom: 0;
        width: 150%;
        height: 2px;
        background: #ffffff80;
        transform: translateX(-50%);
        animation: slideIn 8s ease-out infinite;
      }

      @keyframes gradientShift {
        0% {
          background-position: 0%;
        }
        50% {
          background-position: 100%;
        }
        100% {
          background-position: 0%;
        }
      }

      @keyframes slideIn {
        0% {
          width: 0;
          opacity: 0;
        }
        50% {
          width: 100%;
          opacity: 1;
        }
        100% {
          width: 150%;
          opacity: 0;
        }
      }
    }
  }

  .main {
    text-align: center;
    padding-bottom: 7%;
    flex: 1;

    img {
      width: 50%;
    }

    .enter_text {
      font-weight: 400;
      text-transform: uppercase;
      color: #ffcc00;
    }

    .link {
      position: relative;
      padding: 15px 30px;
      font-size: 18px;
      font-family: Arial, sans-serif;
      color: #fff;
      text-transform: uppercase;
      letter-spacing: 3px;
      background-color: #282828;
      border-radius: 10px;
      text-decoration: none;
      display: inline-block;
      overflow: hidden;
      box-shadow: 0 0 10px #00ffffb3, 0 0 30px #00ffff80, 0 0 40px #00ffff4d, 0 0 50px #00ffff33;
      transition: 0.4s;

      &::before,
      &::after {
        content: "";
        position: absolute;
        left: -100%;
        width: 100%;
        height: 2.5px;
        background: linear-gradient(to right, transparent, #00ffff, transparent);
        animation: neonBorder 3s linear infinite;
      }

      &::before {
        top: 0;
      }

      &::after {
        bottom: 0;
        animation-delay: 1.5s;
      }

      @keyframes neonBorder {
        0% {
          left: -100%;
        }
        50% {
          left: 100%;
        }
        100% {
          left: -100%;
        }
      }

      &:hover {
        box-shadow: 0 0 30px #00ffff, 0 0 40px #00ffffe6, 0 0 50px #00ffffb3, 0 0 60px #00ffff80;
      }
    }
  }

  @media (max-width: 768px) {
    .header .title {
      font-size: 28px;
      letter-spacing: 8px;
    }

    .main {
      img {
        width: 40%;
      }

      .link {
        font-size: 14px;
        padding: 12px 25px;
      }

      .enter_text {
        font-size: 14px;
      }
    }
  }
}
